/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase } from 'change-case';
import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';
// @mui
import { useTheme } from '@mui/material/styles';
import {
    Card,
    Table,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Switch,
    Tabs,
    Button,
    Tab,
} from '@mui/material';

// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_DASHBOARD, PATH_ADMIN } from '../../../routes/paths';
// hooks
import useSettings from '../../../hooks/useSettings';
// components
import Page from '../../../components/Page';
import Label from '../../../components/Label';
import Scrollbar from '../../../components/Scrollbar';
import SearchNotFound from '../../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../../components/HeaderBreadcrumbs';
// format number 
import { fCurrency, } from '../../../utils/formatNumber';
// sections
import { TransHead, PlanToolbar, Refund } from '../../../sections/admin/user/list';


// axios

import axios from '../../../utils/axios';



// ----------------------------------------------------------------------

const TABLE_HEAD = [
    { id: 'username', label: 'Username', alignRight: false },
    { id: 'transid', label: 'Reference', alignRight: false },
    { id: 'role', label: 'Action', alignRight: false },
    { id: 'message', label: 'Description', alignRight: false },
    { id: 'amount', label: 'Amount (NGN)', alignRight: false },
    { id: 'oldbal', label: 'Old Bal', alignRight: false },
    { id: 'newbal', label: 'New Bal', alignRight: false },
    { id: 'date', label: 'Date', alignRight: false },
    { id: 'plan_status', label: 'Status', alignRight: false },
];
const ACCOUNT_TABS = [
    {
        value: 'ALL',
        label: 'ALL'
    },
    {
        value: 1,
        label: 'success'
    },
    {
        value: 0,
        label: 'processing'
    },
    {
        value: 2,
        label: 'fail'
    }
];
// ----------------------------------------------------------------------

export default function AdminCardTransaction() {
    const theme = useTheme();
    const { themeStretch } = useSettings();
    const [userList, setUserList] = useState([]);
    const [page, setPage] = useState(0);
    const [load, SetLoad] = useState(true);
    const [totalPage, SetTotal] = useState(0);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [dense, Setdense] = useState(false);
    const [isNotFound, SetNotFound] = useState(false);
    const [status, setStatus] = useState('ALL');
    const { enqueueSnackbar } = useSnackbar();

    const AccessToken = window.localStorage.getItem('accessToken');

    useEffect(() => {
        initialize(page, rowsPerPage, status, filterName);
    }, []);

    const initialize = async (pag, Habukhan = 5, status, search) => {
        let Habukhan_page;
        if (pag > page) {
            Habukhan_page = pag + 1;
        } else {
            Habukhan_page = pag;
        }
        SetLoad(true);
        try {
            const data = await axios.get(`/api/admin/all/card/trans/by/system/${AccessToken}/secure?page=${Habukhan_page}&limit=${Habukhan}&status=${status}&search=${search}`)

            setUserList(data.data?.card_trans.data);
            SetTotal(data.data?.card_trans?.total)
            SetLoad(false);
            setPage(pag);
            SetNotFound(data.data?.card_trans?.total === 0)
            SetLoad(false);

        } catch (_) {
            SetLoad(false);
            setUserList([])
        }
    }

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        initialize(0, event.target.value, status, filterName)
        setPage(0);
    };

    const handleFilterByName = async (filterName) => {
        setFilterName(filterName);
        setPage(0);
        if (filterName) {
            initialize(page, rowsPerPage, status, filterName);
        } else {
            initialize(page, rowsPerPage, status, filterName);
            SetNotFound(false)
            SetLoad(false);
        }
    };

    function handleChangeDense(event) {
        Setdense(event.target.checked);
    }


    const onFail = (data) => {
        SetLoad(true)
        axios.post(`/api/admin/card_refund/${AccessToken}/secure`, {
            plan_status: 2,
            transid: data
        }).then(() => {
            SetLoad(false)
            enqueueSnackbar('Refunded')
            initialize(page, rowsPerPage, status, filterName);
        }).catch((error) => {
            SetLoad(false)
            initialize(page, rowsPerPage, status, filterName);
            if (error.data?.message) {
                enqueueSnackbar(error.data.message, { variant: 'error' })
            } else if (error?.message) {
                enqueueSnackbar(error.message, { variant: 'error' })
            } else {
                enqueueSnackbar('An Error Occured', { variant: 'error' })
            }
        })
    }

    const onSuccess = (data) => {
        SetLoad(true)
        axios.post(`/api/admin/card_refund/${AccessToken}/secure`, {
            plan_status: 1,
            transid: data
        }).then(() => {
            SetLoad(false)
            enqueueSnackbar('Success')
            initialize(page, rowsPerPage, status, filterName);
        }).catch((error) => {
            SetLoad(false)
            initialize(page, rowsPerPage, status, filterName);
            if (error.data?.message) {
                enqueueSnackbar(error.data.message, { variant: 'error' })
            } else if (error?.message) {
                enqueueSnackbar(error.message, { variant: 'error' })
            } else {
                enqueueSnackbar('An Error Occured', { variant: 'error' })
            }
        })
    }

    return (
        <Page title="Transaction: Virtual Cards">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Virtual Card Transactions"
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.general.app },
                        { name: 'Card History' },
                    ]}
                />

                <Card>
                    <Tabs
                        value={status}
                        scrollButtons="auto"
                        variant="scrollable"
                        allowScrollButtonsMobile
                        onChange={(e, value) => { setStatus(value); initialize(page, rowsPerPage, value, filterName); }}
                    >
                        {ACCOUNT_TABS.map((tab) => (
                            <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
                        ))}
                    </Tabs>
                    <PlanToolbar
                        filterName={filterName}
                        onFilterName={handleFilterByName}
                    />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table
                                size={dense ? 'small' : 'medium'}
                            >
                                <TransHead
                                    headLabel={TABLE_HEAD}
                                    rowCount={userList.length}
                                />

                                {!load ? <TableBody>
                                    {userList.map((row) => {
                                        const { username, message, amount, oldbal, newbal, habukhan_date, transid, plan_status, role } = row;

                                        return (
                                            <TableRow
                                                hover
                                                key={transid}
                                                tabIndex={-1}
                                                role="checkbox"
                                            >

                                                <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                                                    <Typography variant="subtitle2" noWrap>
                                                        {username}
                                                    </Typography>
                                                </TableCell>
                                                <TableCell align='left'>
                                                    <Typography variant="subtitle2" noWrap>
                                                        {transid}
                                                    </Typography>
                                                </TableCell>
                                                <TableCell align="left">{capitalCase(role || '')}</TableCell>
                                                <TableCell align="left">{message}</TableCell>
                                                <TableCell align="left">₦{fCurrency(amount)}</TableCell>
                                                <TableCell align="left">₦{fCurrency(oldbal)}</TableCell>
                                                <TableCell align="left">₦{fCurrency(newbal)}</TableCell>
                                                <TableCell align="left">{habukhan_date}</TableCell>
                                                <TableCell align="left">
                                                    <Label
                                                        variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                        color={(plan_status === 1 ? 'success' : 'error')}
                                                    >
                                                        {sentenceCase((plan_status === 0 && 'processing') || (plan_status === 1 && 'successful') || (plan_status === 2 && 'failed'))}
                                                    </Label>
                                                </TableCell>
                                                <Refund plan_status={plan_status} onFail={() => onFail(transid)} onSuccess={() => onSuccess(transid)} />
                                            </TableRow>
                                        );
                                    })}


                                </TableBody> : <TableRow>
                                    <TableCell align="center" colSpan={9} sx={{ py: 3 }}>
                                        <Typography variant="body2">Loading ...</Typography>
                                    </TableCell>
                                </TableRow>}
                                {isNotFound && (

                                    <TableRow>
                                        <TableCell align="center" colSpan={9} sx={{ py: 3 }}>
                                            <SearchNotFound searchQuery={filterName} />
                                        </TableCell>
                                    </TableRow>

                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <FormControlLabel
                        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
                        label="Dense padding" align="left"
                        sx={{ px: 2, py: 1 }}
                    />
                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25, 100]}
                        component="div"
                        count={totalPage}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={(e, page) => initialize(page, rowsPerPage, status, filterName)}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />

                </Card>
            </Container>
        </Page>
    );
}
